import * as Phaser from "phaser";

export interface SynthParameter {
  value: number;
  min: number;
  max: number;
  label: string;
  color: number;
}

export class SynthControlPanel {
  private scene: Phaser.Scene;
  private panel: Phaser.GameObjects.Container;
  private knobs: Map<string, Phaser.GameObjects.Graphics> = new Map();
  private knobLabels: Map<string, Phaser.GameObjects.Text> = new Map();
  private knobValues: Map<string, Phaser.GameObjects.Text> = new Map();
  private waveformButtons: Phaser.GameObjects.Graphics[] = [];
  private waveformLabels: Phaser.GameObjects.Text[] = [];
  private meters: Map<string, Phaser.GameObjects.Graphics> = new Map();
  private meterLabels: Map<string, Phaser.GameObjects.Text> = new Map();
  
  // SYNTH PARAMETERS
  private parameters: Map<string, SynthParameter> = new Map();
  private currentWaveform: string = "sawtooth";
  private waveforms: string[] = ["sawtooth", "square", "triangle", "sine"];
  
  // VISUAL CONSTANTS
  private readonly KNOB_SIZE = 60;
  private readonly KNOB_RADIUS = 30;
  private readonly METER_WIDTH = 8;
  private readonly METER_HEIGHT = 100;
  private readonly BUTTON_SIZE = 40;
  
  constructor(scene: Phaser.Scene, x: number, y: number) {
    this.scene = scene;
    
    // CREATE MAIN PANEL CONTAINER
    this.panel = scene.add.container(x, y);
    this.panel.setScrollFactor(0); // DON'T SCROLL WITH CAMERA
    
    // INITIALIZE PARAMETERS
    this.initializeParameters();
    
    // CREATE VISUAL CONTROLS
    this.createPanelBackground();
    this.createKnobs();
    this.createWaveformButtons();
    this.createMeters();
    this.createLabels();
  }
  
  private initializeParameters(): void {
    this.parameters.set("lfo", {
      value: 2.0,
      min: 0.1,
      max: 10.0,
      label: "LFO",
      color: 0x3498db // BLUE
    });
    
    this.parameters.set("chorus", {
      value: 0.1,
      min: 0.0,
      max: 1.0,
      label: "CHORUS",
      color: 0xe74c3c // RED
    });
    
    this.parameters.set("delay", {
      value: 0.3,
      min: 0.0,
      max: 1.0,
      label: "DELAY",
      color: 0xf39c12 // ORANGE
    });
    
    this.parameters.set("reverb", {
      value: 0.3,
      min: 0.0,
      max: 1.0,
      label: "REVERB",
      color: 0x9b59b6 // PURPLE
    });
  }
  
  private createPanelBackground(): void {
    // MAIN PANEL BACKGROUND
    const panelBg = this.scene.add.graphics();
    panelBg.fillStyle(0x2c3e50, 0.9);
    panelBg.fillRoundedRect(-10, -10, 400, 200, 10);
    panelBg.lineStyle(2, 0x34495e);
    panelBg.strokeRoundedRect(-10, -10, 400, 200, 10);
    
    // TITLE
    const title = this.scene.add.text(0, -80, "SYNTH CONTROL PANEL", {
      fontSize: "18px",
      color: "#ecf0f1",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1
    });
    title.setOrigin(0.5);
    
    this.panel.add([panelBg, title]);
  }
  
  private createKnobs(): void {
    const knobPositions = [
      { x: -120, y: 20, param: "lfo" },
      { x: -40, y: 20, param: "chorus" },
      { x: 40, y: 20, param: "delay" },
      { x: 120, y: 20, param: "reverb" }
    ];
    
    knobPositions.forEach(({ x, y, param }) => {
      const parameter = this.parameters.get(param)!;
      
      // KNOB BACKGROUND
      const knobBg = this.scene.add.graphics();
      knobBg.fillStyle(0x34495e, 0.8);
      knobBg.fillCircle(x, y, this.KNOB_RADIUS + 5);
      knobBg.lineStyle(2, 0x2c3e50);
      knobBg.strokeCircle(x, y, this.KNOB_RADIUS + 5);
      
      // KNOB FACE
      const knob = this.scene.add.graphics();
      knob.fillStyle(0xecf0f1, 0.9);
      knob.fillCircle(x, y, this.KNOB_RADIUS);
      knob.lineStyle(2, parameter.color);
      knob.strokeCircle(x, y, this.KNOB_RADIUS);
      
      // KNOB INDICATOR LINE
      const angle = this.valueToAngle(parameter.value, parameter.min, parameter.max);
      const indicatorX = x + Math.cos(angle) * (this.KNOB_RADIUS - 8);
      const indicatorY = y + Math.sin(angle) * (this.KNOB_RADIUS - 8);
      
      knob.lineStyle(4, parameter.color);
      knob.lineBetween(x, y, indicatorX, indicatorY);
      
      // KNOB CENTER DOT
      knob.fillStyle(parameter.color);
      knob.fillCircle(x, y, 4);
      
      // PARAMETER LABEL
      const label = this.scene.add.text(x, y + 45, parameter.label, {
        fontSize: "12px",
        color: "#ecf0f1",
        fontFamily: "Nabla, system-ui",
        stroke: "#000000",
        strokeThickness: 1
      });
      label.setOrigin(0.5);
      
      // VALUE DISPLAY
      const valueText = this.scene.add.text(x, y + 60, this.formatValue(parameter.value, param), {
        fontSize: "10px",
        color: parameter.color,
        fontFamily: "Nabla, system-ui",
        stroke: "#000000",
        strokeThickness: 1
      });
      valueText.setOrigin(0.5);
      
      this.knobs.set(param, knob);
      this.knobLabels.set(param, label);
      this.knobValues.set(param, valueText);
      
      this.panel.add([knobBg, knob, label, valueText]);
    });
  }
  
  private createWaveformButtons(): void {
    const buttonX = -120;
    const buttonY = 80;
    const spacing = 50;
    
    this.waveforms.forEach((waveform, index) => {
      const x = buttonX + (index * spacing);
      const isActive = waveform === this.currentWaveform;
      
      // BUTTON BACKGROUND
      const button = this.scene.add.graphics();
      button.fillStyle(isActive ? 0x2ecc71 : 0x34495e, 0.8);
      button.fillRoundedRect(x - 20, buttonY - 15, 40, 30, 5);
      button.lineStyle(2, isActive ? 0x27ae60 : 0x2c3e50);
      button.strokeRoundedRect(x - 20, buttonY - 15, 40, 30, 5);
      
      // BUTTON LABEL
      const label = this.scene.add.text(x, buttonY, waveform.toUpperCase(), {
        fontSize: "10px",
        color: isActive ? "#ffffff" : "#bdc3c7",
        fontFamily: "Nabla, system-ui",
        stroke: "#000000",
        strokeThickness: 1
      });
      label.setOrigin(0.5);
      
      this.waveformButtons.push(button);
      this.waveformLabels.push(label);
      
      this.panel.add([button, label]);
    });
    
    // WAVEFORM SECTION LABEL
    const waveformLabel = this.scene.add.text(buttonX - 30, buttonY - 35, "WAVEFORM", {
      fontSize: "12px",
      color: "#ecf0f1",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1
    });
    this.panel.add(waveformLabel);
  }
  
  private createMeters(): void {
    const meterX = 180;
    const meterY = 20;
    const spacing = 40;
    
    const meterParams = ["lfo", "chorus", "delay", "reverb"];
    
    meterParams.forEach((param, index) => {
      const x = meterX + (index * spacing);
      const parameter = this.parameters.get(param)!;
      
      // METER BACKGROUND
      const meterBg = this.scene.add.graphics();
      meterBg.fillStyle(0x2c3e50, 0.8);
      meterBg.fillRect(x - this.METER_WIDTH/2, y - this.METER_HEIGHT/2, this.METER_WIDTH, this.METER_HEIGHT);
      meterBg.lineStyle(1, 0x34495e);
      meterBg.strokeRect(x - this.METER_WIDTH/2, y - this.METER_HEIGHT/2, this.METER_WIDTH, this.METER_HEIGHT);
      
      // METER FILL
      const meter = this.scene.add.graphics();
      const fillHeight = (parameter.value / parameter.max) * this.METER_HEIGHT;
      meter.fillStyle(parameter.color, 0.8);
      meter.fillRect(x - this.METER_WIDTH/2 + 1, y + this.METER_HEIGHT/2 - fillHeight, this.METER_WIDTH - 2, fillHeight);
      
      // METER LABEL
      const label = this.scene.add.text(x, y + 60, parameter.label, {
        fontSize: "10px",
        color: "#ecf0f1",
        fontFamily: "Nabla, system-ui",
        stroke: "#000000",
        strokeThickness: 1
      });
      label.setOrigin(0.5);
      
      this.meters.set(param, meter);
      this.meterLabels.set(param, label);
      
      this.panel.add([meterBg, meter, label]);
    });
  }
  
  private createLabels(): void {
    // CONTROL INSTRUCTIONS
    const instructions = this.scene.add.text(0, 150, "CONTROLS: 5/6 LFO | 7/8 Chorus | 1/2 Delay | 3/4 Reverb | 0 Waveform", {
      fontSize: "12px",
      color: "#95a5a6",
      fontFamily: "Nabla, system-ui",
      stroke: "#000000",
      strokeThickness: 1
    });
    instructions.setOrigin(0.5);
    
    this.panel.add(instructions);
  }
  
  private valueToAngle(value: number, min: number, max: number): number {
    const normalized = (value - min) / (max - min);
    return (normalized * Math.PI * 1.5) - (Math.PI * 0.75); // 270 degree range
  }
  
  private formatValue(value: number, param: string): string {
    if (param === "lfo") {
      return `${value.toFixed(1)}Hz`;
    } else {
      return `${(value * 100).toFixed(0)}%`;
    }
  }
  
  public updateParameter(param: string, value: number): void {
    const parameter = this.parameters.get(param);
    if (!parameter) return;
    
    parameter.value = value;
    
    // UPDATE KNOB
    const knob = this.knobs.get(param);
    if (knob) {
      knob.clear();
      knob.fillStyle(0xecf0f1, 0.9);
      knob.fillCircle(0, 0, this.KNOB_RADIUS);
      knob.lineStyle(2, parameter.color);
      knob.strokeCircle(0, 0, this.KNOB_RADIUS);
      
      const angle = this.valueToAngle(value, parameter.min, parameter.max);
      const indicatorX = Math.cos(angle) * (this.KNOB_RADIUS - 8);
      const indicatorY = Math.sin(angle) * (this.KNOB_RADIUS - 8);
      
      knob.lineStyle(4, parameter.color);
      knob.lineBetween(0, 0, indicatorX, indicatorY);
      
      knob.fillStyle(parameter.color);
      knob.fillCircle(0, 0, 4);
    }
    
    // UPDATE VALUE TEXT
    const valueText = this.knobValues.get(param);
    if (valueText) {
      valueText.setText(this.formatValue(value, param));
    }
    
    // UPDATE METER
    const meter = this.meters.get(param);
    if (meter) {
      meter.clear();
      const fillHeight = (value / parameter.max) * this.METER_HEIGHT;
      meter.fillStyle(parameter.color, 0.8);
      meter.fillRect(-this.METER_WIDTH/2 + 1, this.METER_HEIGHT/2 - fillHeight, this.METER_WIDTH - 2, fillHeight);
    }
  }
  
  public updateWaveform(waveform: string): void {
    this.currentWaveform = waveform;
    
    // UPDATE WAVEFORM BUTTONS
    this.waveforms.forEach((wf, index) => {
      const button = this.waveformButtons[index];
      const label = this.waveformLabels[index];
      const isActive = wf === waveform;
      
      button.clear();
      button.fillStyle(isActive ? 0x2ecc71 : 0x34495e, 0.8);
      button.fillRoundedRect(-20, -15, 40, 30, 5);
      button.lineStyle(2, isActive ? 0x27ae60 : 0x2c3e50);
      button.strokeRoundedRect(-20, -15, 40, 30, 5);
      
      label.setColor(isActive ? "#ffffff" : "#bdc3c7");
    });
  }
  
  public getParameter(param: string): number {
    const parameter = this.parameters.get(param);
    return parameter ? parameter.value : 0;
  }
  
  public getCurrentWaveform(): string {
    return this.currentWaveform;
  }
  
  public setVisible(visible: boolean): void {
    this.panel.setVisible(visible);
  }
  
  public destroy(): void {
    this.panel.destroy();
  }
}
